@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0 auto;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  object-fit: contain;
}

a {
  text-decoration: none;
}

input[type=number] {
  -moz-appearance: textfield;
  /*stylelint-disable-line */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header__container {
  margin: 0 auto;
  max-width: 320px;
  min-height: 65px;
  font-family: "Oswald", "Lato", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  background-color: #ffffff;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header__container {
    display: flex;
    padding: 0 30px;
    max-width: 708px;
    align-items: center;
    font-family: "Oswald", "Lato", "Arial", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .header__container {
    justify-content: space-between;
    max-width: 1220px;
    min-height: 60px;
    padding: 53px 110px;
    font-family: "Oswald", "Lato", "Arial", sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
}

.header__navigation-logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 190px;
  padding: 12px 20px;
}
.header__navigation-logo:hover {
  opacity: 0.7;
}
.header__navigation-logo:active {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .header__navigation-logo {
    margin-top: -5px;
    padding: 0;
    width: 174px;
  }
}
@media (min-width: 1440px) {
  .header__navigation-logo {
    min-width: 202px;
    margin: 0;
  }
}

.header__navigation-icon:nth-child(2) {
  padding-left: 60px;
}
@media (min-width: 768px) {
  .header__navigation-icon:nth-child(2) {
    padding-left: 12px;
  }
}

@media (min-width: 768px) {
  .header__navigation-slogan {
    width: 100px;
    height: 21px;
  }
}
@media (min-width: 1440px) {
  .header__navigation-slogan {
    width: 119px;
    height: 21px;
  }
}

.header__navigation-menu {
  position: relative;
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__navigation-menu {
    width: 490px;
  }
}
@media (min-width: 1440px) {
  .header__navigation-menu {
    margin: 0;
    color: #ffffff;
    z-index: 1;
  }
}
.header__navigation-menu--catalog, .header__navigation-menu--form {
  color: #000000;
}

.header__navigation-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: stretch;
  text-align: center;
}
@media (min-width: 768px) {
  .header__navigation-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    margin-left: 50px;
    width: 484px;
    min-height: 34px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .header__navigation-list {
    width: 550px;
    justify-content: flex-start;
  }
}

.header__navigation-menu--active .header__navigation-toggle {
  position: absolute;
  display: block;
  width: 24px;
  height: 20px;
  border: 0;
  transform: translate(275px, -40px);
  background: url("../img/svg/nav_cross.svg");
  background-repeat: no-repeat;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__navigation-menu--active .header__navigation-toggle {
    display: none;
  }
}

.header__navigation-menu--inactive .header__navigation-toggle {
  position: absolute;
  display: block;
  width: 24px;
  height: 20px;
  border: 0;
  transform: translate(275px, -40px);
  background: url("../img/svg/nav_hamburger.svg");
  background-repeat: no-repeat;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  cursor: pointer;
}
.header__navigation-menu--inactive .header__navigation-toggle:hover {
  filter: drop-shadow(0 2px 2px black);
}
.header__navigation-menu--inactive .header__navigation-toggle:focus {
  filter: drop-shadow(0 2px 2px black);
}
.header__navigation-menu--inactive .header__navigation-toggle:active {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .header__navigation-menu--inactive .header__navigation-toggle {
    display: none;
  }
}

@media (min-width: 320px) {
  .header__navigation-menu--active .header__navigation-list {
    position: absolute;
    display: flex;
    background-color: #ffffff;
    width: 320px;
    animation: nav_animation 0.6s;
    z-index: 2;
  }
  @keyframes nav_animation {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
}
@media (min-width: 768px) {
  .header__navigation-menu--active .header__navigation-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    position: initial;
    margin-left: 50px;
    width: 484px;
    min-height: 34px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .header__navigation-menu--active .header__navigation-list {
    width: 550px;
    margin-left: -7px;
    justify-content: flex-start;
    background: initial;
  }
}

@media (min-width: 320px) {
  .header__navigation-menu--inactive .header__navigation-list {
    display: none;
  }
}
@media (min-width: 768px) {
  .header__navigation-menu--inactive .header__navigation-list {
    margin: -7px 0 0 57px;
    width: 478px;
    min-height: 34px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .header__navigation-menu--inactive .header__navigation-list {
    width: 550px;
    margin-left: -7px;
    justify-content: flex-start;
  }
}

.header__navigation-menu--nojs .header__navigation-toggle {
  display: none;
}

@media (min-width: 320px) {
  .header__navigation-menu--nojs .header__navigation-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: stretch;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .header__navigation-menu--nojs .header__navigation-list {
    padding: 0;
    margin: -7px 0 0 57px;
    width: 478px;
    min-height: 34px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .header__navigation-menu--nojs .header__navigation-list {
    width: 550px;
    margin-left: -7px;
    justify-content: flex-start;
  }
}

@media (min-width: 768px) {
  .header__navigation-item {
    padding: 10px 19px 10px 16px;
  }
}
@media (min-width: 1440px) {
  .header__navigation-item {
    padding: 10px 17px;
  }
}

.header__navigation-link {
  display: block;
  color: inherit;
  padding: 22px 20px 24px;
  border-top: 1px solid #e6e6e6;
}
.header__navigation-link:hover {
  opacity: 0.7;
}
.header__navigation-link:focus {
  opacity: 0.7;
}
.header__navigation-link:active {
  opacity: 0.4;
}
@media (min-width: 768px) {
  .header__navigation-link {
    padding: 8px 0;
    position: relative;
    border: none;
  }
  .header__navigation-link--checked::before {
    position: absolute;
    height: 2px;
    bottom: 1px;
    left: 0;
    right: 0;
    background-color: #68b738;
    content: "";
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .header__navigation-link--checked::before {
    background-color: #ffffff;
  }
}

@media (min-width: 1440px) {
  .header__navigation-link--catalog-checked::before {
    position: absolute;
    height: 2px;
    bottom: 1px;
    left: 0;
    right: 0;
    background-color: #68b738;
    content: "";
  }
}

@media (min-width: 1440px) {
  .header__navigation-link--form-checked::before {
    position: absolute;
    height: 2px;
    bottom: 1px;
    left: 0;
    right: 0;
    background-color: #68b738;
    content: "";
  }
}

.offer__wrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  width: 280px;
}
@media (min-width: 768px) {
  .offer__wrapper {
    display: grid;
    margin-top: 52px;
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .offer__wrapper {
    width: 1220px;
    margin: 47px auto 265px;
  }
}

.offer__review {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  width: 280px;
  min-height: 302px;
  background-color: #68b738;
}
.offer__review::before {
  position: absolute;
  width: 100vw;
  height: 100%;
  background-image: url("../img/jpg/background-mobile.jpg");
  background-repeat: no-repeat;
  background-position-x: center;
  background-size: cover;
  background-color: #68b738;
  content: "";
}
@supports (background-image: url("../img/jpg/background-mobile.webp")) {
  .offer__review::before {
    background-image: url("../img/jpg/background-mobile.webp");
  }
}
@media (min-resolution: 2dppx) {
  .offer__review::before {
    background-image: url("../img/jpg/background-mobile@2x.jpg");
  }
  @supports (background-image: url("../img/jpg/background-mobile@2x.webp")) {
    .offer__review::before {
      background-image: url("../img/jpg/background-mobile@2x.webp");
    }
  }
}
@media (min-width: 768px) {
  .offer__review::before {
    margin-left: 0;
    display: none;
  }
}
@media (min-width: 1440px) {
  .offer__review::before {
    display: inherit;
    top: 0;
    right: 0;
    width: 50%;
    height: 694px;
    background-image: url("../img/jpg/background-desktop.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: initial;
  }
  @supports (background-image: url("../img/jpg/background-desktop.webp")) {
    .offer__review::before {
      background-image: url("../img/jpg/background-desktop.webp");
    }
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .offer__review::before {
    background-image: url("../img/jpg/background-desktop@2x.jpg");
  }
  @supports (background-image: url("../img/jpg/background-desktop@2x.webp")) {
    .offer__review::before {
      background-image: url("../img/jpg/background-desktop@2x.webp");
    }
  }
}
@media (min-width: 768px) {
  .offer__review {
    display: block;
    width: 420px;
    min-height: 181px;
    padding: 0 88px 30px;
    background: #ffffff;
  }
}
@media (min-width: 1440px) {
  .offer__review {
    position: initial;
    width: 420px;
    min-height: 180px;
    padding: 0 80px;
  }
}

.offer__picture {
  margin-top: -135px;
  z-index: 1;
}
@media (min-width: 768px) {
  .offer__picture {
    margin-top: 0;
    transform: translateY(-85px);
    background: #ffffff;
  }
}
@media (min-width: 1440px) {
  .offer__picture {
    background: none;
    position: absolute;
    transform: translate(448px, 24px);
  }
}

.offer__title {
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: "Oswald", "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  padding: 27px 15px 25px;
  color: #ffffff;
  word-break: break-word;
  z-index: 1;
}
@media (min-width: 768px) {
  .offer__title {
    margin-bottom: 40px;
    padding: 0;
    color: #000000;
    font-size: 60px;
    line-height: 60px;
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .offer__title {
    margin-bottom: 42px;
  }
}

.offer__about {
  padding: 0 40px 20px;
  margin: 0 0 144px;
  font-family: "Oswald", "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: #ffffff;
  z-index: 1;
}
@media (min-width: 768px) {
  .offer__about {
    margin: 0;
    padding: 0;
    margin-bottom: 18px;
    font-size: 20px;
    line-height: 20px;
    text-align: left;
    color: #000000;
  }
}
@media (min-width: 1440px) {
  .offer__about {
    margin-bottom: 52px;
  }
}

.offer__button {
  width: 280px;
  padding: 10px 0;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 18px;
  box-sizing: border-box;
  word-break: break-word;
}
@media (min-width: 768px) {
  .offer__button {
    grid-row: 2/3;
    width: 244px;
    padding: 18px 0;
    margin: 0 0 0 90px;
    margin-top: 0;
    font-size: 20px;
    line-height: 20px;
    z-index: 1;
  }
}
@media (min-width: 1440px) {
  .offer__button {
    margin: 0 0 0 79px;
    width: 244px;
    font-size: 20px;
    line-height: 20px;
  }
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Oswald", "Lato", "Arial", sans-serif;
  color: #ffffff;
  background-color: #68b738;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}

.features__wrapper {
  display: grid;
  justify-content: center;
  grid-gap: 20px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .features__wrapper {
    grid-gap: 30px;
    width: 708px;
    margin: -90px auto 0;
  }
}
@media (min-width: 1440px) {
  .features__wrapper {
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
    width: 1220px;
    margin: 0 auto 70px;
  }
}

.features__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  align-items: flex-start;
  width: 280px;
  min-height: 266px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .features__list {
    position: relative;
    display: grid;
    grid-template-columns: 52px 340px 1fr;
    width: 708px;
    min-height: 372px;
  }
  .features__list:last-child {
    margin-bottom: 45px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
