.features__title {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: min-content auto;
  column-gap: 20px;
  padding: 24px;
  align-items: center;
  justify-content: center;
  text-align: left;
  font-family: "Oswald", "Lato", "Arial", sans-serif;
